<template>
    <div>
        <el-card shadow="always">
            <div slot="header" class="horiz-container vertical-center">
                <span class="header-text">论坛版块管理</span>
                <el-divider direction="vertical" class="header-text"></el-divider>
                <el-button
                    type="danger"
                    :disabled="selected.length <= 0"
                    @click="deleteSelected"
                >批量删除</el-button>
                <el-button
                    type="primary"
                    :disabled="selected.length <= 0"
                    @click="resetSelectedLevel"
                >重置选中的排序值</el-button>
                <div class="spacer"></div>
                <el-button size="medium" type="success" @click="dialog = true">
                    <i class="fa fa-plus"></i>
                    添加新版块
                </el-button>
            </div>

            <el-table
                :data="categories"
                v-loading="loading"
                @selection-change="mutilSelectionChange"
                style="width: 100%"
                stripe
                border
            >
                <el-table-column type="selection" width="35" align="center"></el-table-column>
                <el-table-column prop="title" label="标题"></el-table-column>
                <el-table-column prop="description" label="描述"></el-table-column>
                <el-table-column prop="topicCount" label="话题数量" width="85"></el-table-column>
                <el-table-column prop="level" label="排序值" width="80"></el-table-column>
                <el-table-column label="最后修改" width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.lastEditor.username }}</span>
                        <br />
                        <span>{{ scope.row.updatedAt | datetimeFormat }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            size="medium"
                            title="编辑"
                            @click="editItem(scope.row)"
                        >
                            <i class="fa fa-pencil-square-o fa-lg" style="color:blue; "></i>
                        </el-button>
                        <el-button
                            size="medium"
                            type="text"
                            title="删除"
                            @click="deleteItem(scope.row)"
                        >
                            <i class="fa fa-trash-o fa-lg" style="color:red; "></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                @size-change="pageSizeChange"
                @current-change="currnetPageChange"
                layout="total, sizes, prev, pager, next, jumper"
                :total="totalCount"
                :page-sizes="[5, 10, 20, 30]"
                :page-size.sync="pageSize"
                :current-page.sync="currentPage"
            ></el-pagination>
        </el-card>
        <!-- 添加和修改版块的对话框  开始 -->
        <el-dialog
            :visible.sync="dialog"
            width="80%"
            :close-on-click-modal="false"
            :fullscreen="fullscreen"
        >
            <span slot="title">
                <el-button type="text" @click="fullscreen = true" v-if="!fullscreen">
                    <i class="fa fa-square-o" style="font-size: 20px;" title="最大化"></i>
                </el-button>
                <el-button type="text" @click="fullscreen = false" v-else>
                    <i class="fa fa-clone" style="font-size: 20px;" title="还原"></i>
                </el-button>
                <span class="dialog-title">{{ formTitle }}</span>
            </span>
            <el-card shadow="always">
                <el-form label-position="top">
                    <el-row :gutter="20">
                        <!-- :gutter="20" 同一行内，两列之间的间隔 -->
                        <el-col :md="24 - formCols" :sm="24" v-if="editedIndex !== -1">
                            <el-form-item label="Level">
                                <el-input
                                    type="number"
                                    v-model="editedItem.level"
                                    controls-position="right"
                                    :max="1000"
                                    label="Level"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :md="formCols" :sm="24">
                            <el-form-item label="标题">
                                <el-input
                                    v-model="editedItem.title"
                                    placeholder="标题"
                                    maxlength="50"
                                    show-word-limit
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-form-item label="描述(支持 Markdown 语法)">
                        <el-input
                            type="textarea"
                            v-model="editedItem.description"
                            :rows="row"
                            placeholder="版块描述"
                        ></el-input>
                    </el-form-item>
                </el-form>
            </el-card>
            <span slot="footer">
                <el-button type="danger" @click="dialog = false">取消</el-button>
                <el-button type="primary" @click="saveItem">保存</el-button>
            </span>
        </el-dialog>
        <!-- 添加和修改版块的对话框   结束 -->
    </div>
</template>

<script>
// 使用 @ 开头的路径导入 js 类时， 类的方法没有提示
import CategoryService from "../../../api/service/category_service";
import moment from 'moment';
export default {
    name: 'CategoryManager',
    data: () => ({
        categories: [],      // 所有的版块数据
        totalCount: 0,       // 总记录数量
        // totalPage: 1,        // 总页数, el-pagination 有计算页数功能，这个变量可不要
        pageSize: 10,        // 每页大小
        currentPage: 1,      // 当前页
        dialog: false,       // 添加和修改对话框        
        fullscreen: false,   // 添加 修改 对话框 是否全屏
        loading: false,      // 数据表格 显示 正在加载的动画
        selected: [],        //多选框选中的项
        editedIndex: -1,     // 添加 或 修改 的标志；-1表示添加，否则为当前编辑项在 categories 中的索引
        // 添加或修改绑定到对话框上的数据
        editedItem: {
            title: "",
            description: "",
            level: 0,
        },
        // 用于添加或修改结束后，还原 editedItem 的初始值
        defaultItem: {
            title: "",
            description: "",
            level: 0,
        },

    }),
    mounted() {
        this.getCategories();
    },
    methods: {
        // 在函数内调用异步方法时，要使用 async 进行声明
        async getCategories() {
            this.loading = true;  // 显示 "正在加载" 的动画
            this.categories = [];
            let skip = this.pageSize * (this.currentPage - 1);
            // 查询总记录数
            this.totalCount = await CategoryService.getTotalCount();
            // this.totalPage = Math.ceil(this.totalCount / this.pageSize);
            // 使用 await 调用异步方法
            let response = await CategoryService
                .fetchAll(this.pageSize, skip, ['createdBy', 'lastEditor'], ['-level', '-createdAt']);
            //  CategoryService.fetchAll() 的参数说明：
            // 1、每页大小；  2、跳过记录数；
            // 3、查询时关联到其他表的字段名，数组类型， 例如：['createdBy', 'lastEditor']
            // 4、降序排列的排序字段名列表，例如：['-level', '-createdAt']， 降序的字段名前面加负号
            // 第三、第四个参数，只有一个字段时也要加： []  

            console.log("result: ", response);
            // 将每一项转为 字典类型， 并重新组成数组
            this.categories = response.map(item => { return this.formJson(item) });
            this.loading = false;  // 停止显示正在加载的动画
        },

        // 显示编辑对话框
        editItem(item) {
            // 查找 被 编辑项的索引值
            this.editedIndex = this.categories.indexOf(item);
            // Object.assign() 用于将源对象复制到目标对象
            // 因为直接用等号赋值，这两个变量操作的是同一个内存地址
            this.editedItem = Object.assign({}, item);
            this.dialog = true;  // 显示编辑对话框
        },

        // 保存数据(包含添加和修改两个操作的保存)
        async saveItem() {
            console.log('this.editedIndex:', this.editedIndex);
            if (this.editedIndex === -1) {
                // this.editedIndex === -1 表示当前为添加新项


                /**
                 * 向 leancloud 添加数据时，可以不用提前创建表结构，如果没有表，leancloud 会自动创建表
                 * 没有的字段，则会根据上传的 json 数据在表中添加相应的字段
                 * 为防止字段名在多个地方调用时不一致，可以定义为一个模型类用于传递该值，参考见：src/views/dashboard/forum/TopicManager.vue
                 * 以下变量 category 为要添加的的项，
                 * 每个 leancloud 表会包含以下默认字段(不需要写，自动添加的): 
                 * ObjectId: 主键字段，实际使用时用 表名.id 来获取值；
                 * createdAt: 创建时间，自动生成，不能修改
                 * updatedAt: 最后修改时间，自动生成，自动修改，不能由用户修改
                 */
                let category = {
                    title: this.editedItem.title.trim(),       // 版块标题， 去掉前后空格
                    description: this.editedItem.description,  // 版块描述
                    level: 0,     // 用于手工排序
                    topicCount: 0,     // 当前版块包含的话题数量
                    createdBy: await CategoryService.currentUser(),   // 版块创建者
                    lastEditor: await CategoryService.currentUser(),  // 最后修改者
                };

                if (category.title) {
                    // 先检查 要添加的版块 名称 是否已经存在     
                    let exists = await CategoryService.existsFieldValue('title', category.title);
                    if (exists) {
                        // message 消息提示(显示在窗口上方)
                        this.$message({
                            message: `"${category.title}" 版块已经存在，不能重复添加`,
                            type: 'error'
                        });
                        // 使用 Notification 通知 显示添加失败的的提示信息(Notification 和 message 二选一)
                        // this.$notify.error({
                        //     title: '添加版块失败',      // 标题
                        //     message: '此版块已经存在，不能重复添加',     // 消息内容
                        //     // offset: 100,        // 偏移量，消息距屏幕边缘偏移一段距离
                        //     showClose: false,      // 不显示关闭按钮，只能等自动关闭
                        // });
                    } else {
                        let response = await CategoryService.create(category);
                        console.log('response:', response);
                        if (response) {
                            this.categories.push(this.formJson(response));
                            // 清空对话框的数据
                            this.editedItem = Object.assign({}, this.defaultItem);
                            this.dialog = false;  // 关闭对话框, 如果不需要自动关闭，把这一行注释掉
                            // 使用 Notification 通知 显示添加失败的的提示信息
                            this.$notify.success({
                                title: '添加成功',      // 标题
                                message: `已经成功添加论坛版块: ${category.title}`,     // 消息内容
                                // offset: 100,        // 偏移量，消息距屏幕边缘偏移一段距离
                                showClose: false,      // 不显示关闭按钮，只能等自动关闭
                            });
                        }
                    }
                } else {
                    this.$message({
                        message: '版块标题不能为空',
                        type: 'warning'
                    });
                }
            } else {
                // this.editedIndex 不等于 -1, 表示当前为修改，其值为被修改项在数组中的索引
                // 编辑版块时，不检查标题是否和其他版块同名，因为可以不改标题，只改其他内容
                // 要完成检查所有项是否相同比较麻烦，所以就不检查了

                // Object.assign()合并修改后的数据到变量item中，获取在对话框中已修改的内容
                let item = Object.assign(
                    this.categories[this.editedIndex],
                    this.editedItem
                );

                // updatedAt: 这个是最后修改时间，不能手动修改，是由leancloud 自动完成修改的
                let response = await CategoryService.update(item.id, {
                    title: item.title,              // 修改后的标题
                    description: item.description,  // 修改后的描述
                    level: Number(item.level),      // 修改后的level， 要转为数字类型
                    lastEditor: await CategoryService.currentUser(), // 最后修改者
                });

                if (response) {
                    // 更新 当前数据中的 最后修改时间（updatedAt)
                    this.categories[this.editedIndex].updatedAt = response.updatedAt;
                    // 将结果按照 level 重新排序
                    this.categories.sort((a, b) => {
                        return b.level - a.level;
                    });
                    this.dialog = false;   // 关闭对话框
                    this.$message({
                        message: '论坛版块编辑成功',
                        type: 'success'
                    });
                } else {
                    this.$message({
                        message: '出现错误, 无法完成论坛版块修改',
                        type: 'error'
                    });
                }
            }
        },

        // 删除记录
        deleteItem(item) {
            // 在数组中查找删除对象的索引
            this.editedIndex = this.categories.indexOf(item);
            this.editedItem = Object.assign({}, item);  // 复制要删除的对象
            // this.deleteItemConfirm = `确定要删除名为："${item.title}" 的版块吗？`;
            // 显示删除确认对话框
            this.$confirm(`确定要删除名为："${item.title}" 的版块吗？`, '删除版块', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // .then()  里面的匿名函数前要加  async 
                // 在服务器端执行删除操作
                let response = await CategoryService.delete(item.id);
                if (response) {
                    // 从当前显示 的数据中删除对应的项
                    this.categories.splice(this.editedIndex, 1)[0];
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },

        // 用于在表格中选中多行的函数
        mutilSelectionChange(val) {
            this.selected = val;   // 保存选中的行
            console.log(this.selected);
        },

        // 批量删除已选中的多行
        deleteSelected() {

            this.$confirm(`确定要删除已选中的 ${this.selected.length} 个版块吗？`, '批量删除版块', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {     // .then()  里面的匿名函数前要加  async 
                // 在服务器端执行删除操作
                let response = await CategoryService.deleteBatch(this.selected);
                if (response) {
                    this.getCategories();  // 重新载入数据 
                    this.selected = [];  // 重置变量
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });

                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },

        // 重置选中的多个level 值为 0， 批量修改的应用
        resetSelectedLevel() {
            this.$confirm(`确定要将已选中的 ${this.selected.length} 个版块的 Level 值重置为 0 吗？`, '重置Level值', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {     // .then()  里面的匿名函数前要加  async 

                // 修改所有选中项的 level 值
                this.selected.forEach(async item => {
                    console.log('item: ', item);
                    item.level = 0           // 修改所有选中行的 level 值 
                    delete item.createdAt;   // 删除只能由服务器端自动修改的字段
                    delete item.updatedAt;   // 删除只能由服务器端自动修改的字段                    
                    delete item.createdBy;   // 删除 创建者

                    item.lastEditor = await CategoryService.currentUser();  //  最后的修改者
                    return item;
                });

                // 将包含已修改值之后的所有项传到服务器端执行批量修改操作
                let response = await CategoryService.updateBatch(this.selected);
                if (response) {
                    this.getCategories();  // 重新载入数据 
                    this.selected = [];  // 重置变量
                    this.$message({
                        type: 'success',
                        message: '重置成功!'
                    });
                }
            }).catch((e) => {
                console.log('出现异常:', e);
                this.$message({
                    type: 'info',
                    message: '已取消重置操作'
                });
            });
        },

        // 关闭添加或修改的对话框，并还原对话框数据为初始值
        closeDialog() {
            this.dialog = false;
            this.fullscreen = false;
            // this.$nextTick()将回调延迟到下次 DOM 更新循环之后执行
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        // 修改每页大小时的方法
        pageSizeChange(val) {
            // 如果使用 带 .sync 修饰符的绑定，如：  :page-size.sync="pageSize" 
            // 则不需要用下一行的方式给 页大小赋值
            // this.pageSize = val;  
            this.getCategories();
        },

        // 修改当前页时的方法
        currnetPageChange(val) {
            // 如果使用 带 .sync 修饰符的绑定，如：  :current-page.sync="currentPage"
            // 则不需要用下一行的方式给 页大小赋值
            // this.currentPage = val;
            this.getCategories();

        },

        // 获取用户表中各个字段的值
        getUser(user) {
            // console.log(user);
            return {
                id: user.id,
                username: user.get("username"),
                email: user.get("email"),
            };
        },

        // 将从服务器获取的一个数据项转为对象格式的数据
        formJson(item) {
            return {
                id: item.id,   // leancloud Class 自带属性，对应 leanclud Class 中的 "ObjectId", 可以用".id"直接引用
                title: item.get("title"),  // title 为自定义的属性名，要使用 get() 来取值 
                description: item.get("description"),
                level: item.get("level"),
                topicCount: item.get("topicCount"),
                createdAt: item.createdAt,   // leancloud Class 自带属性,创建时间,不能修改值
                updatedAt: item.updatedAt,   // leancloud Class 自带属性,最后修改时间,只能leancloud 自动修改

                /**
                 * createdBy、lastEditor 指向另一个表的字段, 默认只能得到该字段在关联表中的id 值
                 * 要得到完整值， 查询时字段名要包含在 include 中，
                 * 取值时，要先判断是否为空，如果为空，取值会抛出异常，
                 */
                createdBy: item.get("createdBy")
                    ? this.getUser(item.get("createdBy"))
                    : "",
                lastEditor: item.get("lastEditor")
                    ? this.getUser(item.get("lastEditor"))
                    : "",
            };
        },



    },

    // 计算属性
    computed: {
        // 添加或修改对话框的标题
        formTitle() {
            return this.editedIndex === -1 ? "添加新版块" : "版块编辑";
        },

        // 添加或修改对话框中 标题 文本框的宽度
        formCols() {
            // 添加时不用显示 Level，Title 宽度为 24
            // 修改时需要显示 Level，Title 宽度为 21
            return this.editedIndex === -1 ? 24 : 21;
        },

        // 多行文本框的行数
        row() {
            // 全屏状态时多行文本框 15 行，非全屏时 5行
            return this.fullscreen ? 15 : 5;
        }
    },

    // 过滤器
    filters: {
        // 使用 moment.js 定义日期时间过滤器函数
        datetimeFormat(val) {
            return moment(val).format("lll");
        },
    },

    // 观察属性， 函数名为变量名，当该变量值有变化时，触发函数
    watch: {
        // 监控变量 dialog, 当对话框关闭时，还原对话框相关的初始值 
        dialog(val) {
            // 相当于 if 语句的简化写法
            val || this.closeDialog();
        },
    }


}
</script>

<style>
.header-text {
    font-size: 24px;
}

.dialog-title {
    margin-left: 12px;
    font-size: 24px;
}

/* 分页组件靠右对齐 */
.el-pagination {
    text-align: right;
}
</style>